* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: 'Poppins', sans-serif;
  src: url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');
}

body {
  background: url("./images/bg-intro-desktop.png") no-repeat, #FF7978;
}

.container {
  width: 55%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 10%;
}

.statement {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 90px;
  margin-right: 85px;
}

h1 {
  color: #fff;
  font-family: 'poppins';
  margin-bottom: 25px;
  font-size: 2.2rem;
}

.p1 {
  color: #fff;
  font-family: 'poppins';
}

