.blue-box {
  width: 170%;
  border-radius: 5px;
  background-color: #5D54A3;
  padding: 15px;
  margin-bottom: 20px;
  box-shadow: 5px 5px rgb(75 64 68 / 30%);
}

.blue-box p {
  color: #fff;
  font-family: 'poppins';
  font-size: 12px;
  text-align: center;
}

.form {
  width: 170%;
  border-radius: 5px;
  background-color: #fff;
  padding: 20px;
  box-shadow: 5px 5px rgb(75 64 68 / 30%);
}

input[type=text], input[type=email], input[type=password] {
  width: 95%;
  padding: 16px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-family: 'poppins';
}

input[type=submit] {
  width: 95%;
  background-color: #37CC8A;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: 'poppins';
}

input[type=submit]:hover {
  background-color: #45a049;
}

.terms {
  text-align: center;
  font-family: 'poppins';
  font-size: .6rem;
  color: darkgray;
  padding-bottom: 10px;
}

/* media query */

@media screen and (min-width: 320px) {
  body {
    background: url("./images/bg-intro-mobile.png") no-repeat, #FF7978;
  }
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .statement {
    width: 150%;
    margin-top: -15%;
    margin-left: 15%;
    margin-bottom: 5%;
  }
  h1 {
    font-size: 30px;
  }
  .blue-box {
    padding: 8px;
  }
  .form {
    margin-bottom: 2%;
    padding: 8px;
  }
  input[type=text], input[type=email], input[type=password] {
    padding: 8px 8px;
  }
  input[type=submit] {
    padding: 8px;
  }
  .container2 {
    margin-left: 10%;
  }
  .terms {
    padding-bottom: 0%;
  }
}

@media screen and (min-width: 375px) {
  body {
    background: url("./images/bg-intro-mobile.png") no-repeat, #FF7978;
  }
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .statement {
    width: 150%;
    margin-left: 15%;
    margin-bottom: 5%;
    margin-top: -20%;
  }
  .container2 {
    margin-left: 10%;
  }
  .terms {
    padding-bottom: 0%;
  }
  .form {
    margin-bottom: 2%;
    padding: 8px;
  }
  input[type=text], input[type=email], input[type=password] {
    padding: 8px 8px;
  }
  input[type=submit] {
    padding: 8px;
  }
  .blue-box {
    padding: 8px;
  }
}

@media screen and (min-width: 1024px) {
  body {
    background: url("./images/bg-intro-desktop.png") no-repeat, #FF7978;
  }
  .container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 85%;
    margin-left: -10%;
  }
  .statement {
    margin-top: 5%;
    margin-right: 0%;
  }
}

@media screen and (min-width: 1440px) {
  body {
    background: url("./images/bg-intro-desktop.png") no-repeat, #FF7978;
  }
  .container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 65%;
    margin-left: 1%;
  }
  .statement {
    margin-top: 5%;
  }
}

